* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

li {
  list-style-type: none;
}

button {
  cursor: pointer;
}

.wrapper {
  display: flex;
  justify-content: center;
  margin: 30px 20px 0px 20px;
  min-height: calc(100vh - 40px);
}

.container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 1280px;
  min-height: 100%;
  padding: 0 10px;
}

@media (max-width: 500px) {
  .container {
    justify-content: stretch;
  }
}